@import url(https://fonts.googleapis.com/css?family=Arbutus:regular);
body {
  margin: 0;
  font-family: "RocknRoll One";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(
    to right,
    #c33764,
    indigo,
    #1d2671,
    #c33764
  );
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: cover;
  background-attachment: fixed;
  color: whitesmoke;
  font-family: "Arbutus";
}

.App {
  text-align: center;
}
h1 {
  font-size: 3em;
  text-shadow: 5px 5px rgba(51, 51, 51, 0.7);
}
.circleGroup {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.circle {
  margin: 1em;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  background-color: gray;
  border: dotted yellowgreen;
}

.active {
  background-color: yellow;
  background-image: url(https://i.gifer.com/WiCJ.gif);
  background-size: cover;
  border: none;
}

.gameOver {
  background-color: black;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
}

.result {
  background-color: lightcoral;
  margin: 3.5em auto;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  border: dotted;
  width: 80%;
  height: 80%;
  border-radius: 10px;
}

#close {
  top: 0;
  right: 0;
  position: absolute;
  margin: 0.5em auto;
  background-color: transparent;
  border: transparent;
  outline: none;
  color: white;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 800;
  cursor: pointer;
}

button {
  margin: 3em 3em;
  font-family: "Arbutus";
  font-size: 1em;
  width: 100px;
  height: 40px;
  cursor: pointer;
  border-radius: 5px;
  outline: none;
  border: none;
}

button:hover {
  color: rgba(184, 135, 11, 0.7);
}

